.burger {
  --burger-width: 64px;
  --burger-height: 30px;
  --burger-line-height: 1px;
  position: relative;
  border: none;
  padding: 0;
  width: 64px;
  height: 30px;
  color: #fff;
  background-color: transparent;
  cursor: pointer;
}
.burger::before, .burger::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: currentColor;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger::before {
  top: 10px;
}
.burger::after {
  top: calc(100% - 10px);
}
.burger--active::before {
  top: 50%;
  transform: rotate(45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger--active::after {
  top: 50%;
  transform: rotate(-45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger--active .burger__line {
  transform: scale(0);
  transition: transform 0.3s ease-in-out;
}

.burger {
  display: none;
}
.burger__menu {
  position: absolute;
  padding-top: 20px;
  top: calc(var(--header-height) - 10px);
  right: 0;
  width: 320px;
  height: 100vh;
  background-color: var(--brown-color);
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s;
  padding-right: var(--container-offset);
  display: flex;
  flex-direction: column;
  align-items: end;
}
.burger__menu.menu--active {
  opacity: 1;
  visibility: visible;
}
.burger__menu .header__social {
  display: none;
}
.burger__menu nav ul {
  display: flex;
  flex-direction: column;
  align-items: end;
  gap: 40px;
}
.burger__menu nav ul a {
  position: relative;
  font-weight: 300;
  font-size: 22px;
  color: #fff;
  transition: all 0.3s;
}
.burger__menu nav ul a:hover::after {
  opacity: 1;
  visibility: visible;
}
.burger__menu nav ul a::after {
  content: "";
  position: absolute;
  left: 0;
  top: 30px;
  width: 100%;
  height: 1px;
  background-color: #fff;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s;
}

