.housing {
  padding-top: 60px;
}
.housing__title {
  display: flex;
  justify-content: space-between;
  gap: 221px;
  align-items: center;
}
.housing__title > p {
  font-weight: 300;
  font-size: 22px;
  line-height: 26px;
  color: var(--gray-color);
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  margin: 0;
  max-width: 40%;
}

.slider {
  margin-top: 20px;
}

@media (max-width: 1280px) {
  .housing__title {
    flex-direction: column;
    align-items: start;
    gap: 8px;
  }
  .housing__title > p {
    font-size: 20px;
    line-height: 23px;
    max-width: 70%;
  }
}
@media (max-width: 768px) {
  .housing {
    padding-top: 40px;
  }
  .housing__title > p {
    font-size: 16px;
    line-height: 18.4px;
    max-width: 90%;
  }
}